import React from 'react';
import ProjectStyles from "../../styles/projectStyles";
import './style.scss';

export function ProjectBoxEntry(props) {
    return (
        <div
            className="portfolio-item"
            style={{ backgroundColor: ProjectStyles.colorPrimary, color: ProjectStyles.bgPrimary }}
        >
            <div className="portfolio-item__title">{props.title}</div>

            <div className="portfolio-item__desc" dangerouslySetInnerHTML={{__html: props.subtitle}}>
            </div>
            <div className="portfolio-item__icon">
                <i className="fab fa-js" />
                <i className="fab fa-react" />
                <i className="fab fa-html5" />
            </div>
            <div className="portfolio-item__links">
                <a src="#">Code</a>
                <a src="#">Blog</a>
            </div>
        </div>)

}


